import React, { Component } from 'react';
import {variables} from "./variables";

export class UserInfo extends Component {

    constructor(props) {
        super(props);

        this.state = {
            userInfos:[]
        }
    }

    refreshList(){
        fetch(variables.API_URL+'UserInfo')
            .then(response=>response.json())
            .then(data=>{this.setState({userInfos:data});});
    }

    componentDidMount() {
        this.refreshList();
    }

    render() {
        console.log(this.state)

        return (
            <div>
                <table className="table table-striped">
                    <thead>
                    <tr>
                        <th>
                            UserID
                        </th>
                        <th>
                            Date Registration
                        </th>
                        <th>
                            Date Last Activity
                        </th>
                    </tr>
                    </thead>
                    <tbody>
                        {this.state.userInfos.map(user=>
                        <tr key={user.Uid}>
                            <td>{user.userID}</td>
                            <td>{user.dateregistration}</td>
                            <td>{user.datelastactivity}</td>
                        </tr>)}
                    </tbody>
                </table>
            </div >
        );
    }
}
